import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import Mutation from '../components/Mutation';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import UserAddIcon from '@material-ui/icons/PersonAdd';
import AccountBalance from '@material-ui/icons/AccountBalance';
import { withStyles } from '@material-ui/core/styles';
import VpnKey from '@material-ui/icons/VpnKey';
import EnhancedTable from '../components/EnhancedTable';
import EnhancedMenu, { CreateMenuItem } from '../components/EnhancedMenu';
import Query from '../components/Query';
import ChangePassword from '../components/Dialogs/ChangePassword';
import { defaultState } from '../graphql/resolvers';
import filter from '../utils/filter';

import {
  USERS,
  USER_CREDIT_HISTORY,
  SITES,
  PROPERTIES,
  PROPERTY_CREDIT_HISTORY
} from '../graphql/query/remote';
import {
  USER_ENTRY_TABLE as USER_ENTRY_TABLE_QUERY,
  SITE_TABLE as SITE_TABLE_QUERY,
  PROPERTY_TABLE as PROPERTY_TABLE_QUERY
} from '../graphql/query/local';

import {
  USER_ENTRY_TABLE,
  SITE_TABLE,
  PROPERTY_TABLE
} from '../graphql/mutation/local';
import { DELETE_SITES } from '../graphql/mutation/remote';
import commanRender from '../utils/commanRender';
import TableWrapper from '../components/TableWrapper';
import formatDate from '../utils/date';
import PropertyCreditDialog from '../components/Dialogs/PropertyCreditDialog';

const styles = theme => ({
  root: {
    width: '100%',
    // padding: '0',
  },
  fab: {
    position: 'fixed',
    width: "12rem",
    bottom: theme.spacing.unit * 2,
    right: theme.spacing.unit * 2,
  }
});

export const userColumnData = [
  { id: 'createdAt', numeric: false, disablePadding: false, colSpan: 1, label: 'Created At' },
  { id: 'username', numeric: false, disablePadding: false, colSpan: 1, label: 'Username' },
  { id: 'siteCount', numeric: false, disablePadding: false, colSpan: 1, label: 'Total Sites' },
  { id: 'totalSitesCost', numeric: false, disablePadding: false, colSpan: 1, label: 'Total Sites Cost ()' },
  { id: 'totalReceivedAmount', numeric: false, disablePadding: false, colSpan: 1, label: 'Total Received Amount ()' },
  { id: 'spent', numeric: false, disablePadding: false, colSpan: 1, label: 'Spent Amount ()' },
  { id: 'balance', numeric: false, disablePadding: false, colSpan: 1, label: 'Balance Amount ()' },
];
export const siteColumnData = [
  { id: 'createdAt', numeric: false, disablePadding: false, colSpan: 1, label: 'Created At' },
  { id: 'name', numeric: false, disablePadding: false, colSpan: 1, label: 'Name' },
  { id: 'location', numeric: false, disablePadding: false, colSpan: 1, label: 'Location' },
  { id: 'entryCount', numeric: false, disablePadding: false, colSpan: 1, label: 'Total Entries' },
  { id: 'manager', numeric: false, disablePadding: false, colSpan: 1, label: 'Manager' },
  { id: 'managerSpentAmount', numeric: false, disablePadding: false, colSpan: 1, label: 'Manager Spent Amount ()' },
  { id: 'Total Cost', numeric: false, disablePadding: false, colSpan: 1, label: 'Total Cost ()' },
];
export const propertyColumnData = [
  { id: 'createdAt', numeric: false, disablePadding: false, colSpan: 1, label: 'Created At' },
  { id: 'name', numeric: false, disablePadding: false, colSpan: 1, label: 'Name' },
  { id: 'location', numeric: false, disablePadding: false, colSpan: 1, label: 'Location' },
  { id: 'price', numeric: false, disablePadding: false, colSpan: 1, label: 'Price ()' },
  { id: 'buyer', numeric: false, disablePadding: false, colSpan: 1, label: 'Bought By' },
  { id: 'buyerNumber', numeric: false, disablePadding: false, colSpan: 1, label: "Buyer's Phone Number" },
  { id: 'totalReceivedAmount', numeric: false, disablePadding: false, colSpan: 1, label: 'Total Received Amount ()' },
  { id: 'balance', numeric: false, disablePadding: false, colSpan: 1, label: 'Balance Amount ()' },
  { id: 'nextDueDate', numeric: false, disablePadding: false, colSpan: 1, label: 'Next Due Date' },
  { id: 'note', numeric: false, disablePadding: false, colSpan: 1, label: 'Note' },
];
export const userExclude = ["id", "count", "createdAt", "updatedAt", "__typename"];
export const siteExclude = ["id", "count", "createdAt", "updatedAt", "__typename"];
export const propertyExclude = ["id", "count", "createdAt", "updatedAt", "__typename"];

class Dashboard extends PureComponent {
  render() {
    const { classes, history } = this.props;
    return (
      <div>
        <Grid container className={classes.root}>
          <Grid item xs={12}>
            <Mutation mutation={USER_ENTRY_TABLE}>
              {(lmutate, args) => {
                return (
                  <Query query={USER_ENTRY_TABLE_QUERY}>
                    {({ loading, error, data: { userEntryTable } }) => {
                      const { page, rowsPerPage } = userEntryTable;
                      return (
                        <Query query={USERS} variables={{ limit: rowsPerPage, skip: page * rowsPerPage }}>
                          {
                            ({ loading, data, refetch }) => {
                              // refetch();
                              return (
                                <EnhancedTable
                                  {...this.props}
                                  loading={loading}
                                  data={data.users}
                                  title="Users"
                                  settings={userEntryTable}
                                  count={(data.users.length && data.users[0].count) || data.users.length}
                                  columnData={userColumnData}
                                  exclude={userExclude}
                                  historyable={USER_CREDIT_HISTORY}
                                  menu={
                                    (selected) => {
                                      console.log("selected", selected);
                                      return selected.length === 1 ?
                                      (
                                        <EnhancedMenu
                                          options={[                                            
                                            (props) => {
                                              return (
                                                <ChangePassword
                                                  {...props}
                                                  reset
                                                  positiveHandler={(fn, args) => {                                                    
                                                    return fn({
                                                        variables: {
                                                            id: selected[0],
                                                            data: filter(args, ["confirmNewPassword"])
                                                        }
                                                    })
                                                  }}>
                                                  <CreateMenuItem
                                                    {...props}
                                                    icon={<VpnKey />}
                                                    content="Reset Password"
                                                    tooltip="Reset Password"
                                                  />
                                                </ChangePassword>
                                              )
                                            }
                                          ]}
                                        />
                                      ): (null);
                                    }}
                                  options={{
                                    onCredit: async (fn, args) => {
                                      console.log("calling credit mutate function...");
                                      await fn({ variables: args });
                                      // refetch();
                                    }
                                  }}
                                  render={commanRender({ match: "spent" })}
                                  mutate={async args => {
                                    console.log("calling mutate function...");
                                    await lmutate({ variables: args });
                                    // refetch();
                                  }}>
                                </EnhancedTable>
                              );
                            }
                          }
                        </Query>
                      );
                    }}
                  </Query>
                );
              }}
            </Mutation>
          </Grid>
          <Grid item xs={12}>
            <Mutation mutation={SITE_TABLE}>
              {(lmutate, args) => {
                return (
                  <Query query={SITE_TABLE_QUERY}>
                    {({ loading, error, data: { siteTable } }) => {
                      return (
                        <Mutation mutation={DELETE_SITES}>
                          {(deleteSite, { loading, error }) => {
                            const { page, rowsPerPage } = siteTable;
                            return (
                              <Query query={SITES} variables={{ limit: rowsPerPage, skip: page * rowsPerPage }}>
                                {
                                  ({ loading, error, data, refetch }) => {
                                    // refetch();
                                    return (
                                      <EnhancedTable
                                        {...this.props}
                                        loading={loading}
                                        data={data.sites}
                                        title="Sites"
                                        settings={siteTable}
                                        count={(data.sites.length && data.sites[0].count) || data.sites.length}
                                        columnData={siteColumnData}
                                        exclude={siteExclude}
                                        options={{
                                          onView: e => history.push(`/siteDetail/${e}`),
                                          onEdit: e => history.push(`/site/edit/${e}`)
                                        }}
                                        // onDelete={
                                        //     async args => {
                                        //         console.log("calling deleteMutate function...");
                                        //         await deleteSite({ variables: Object.assign({}, args) });
                                        //         await lmutate({ variables: defaultState.siteTable });
                                        //         refetch();
                                        //     }
                                        // }
                                        render={commanRender({ match: "managerSpentAmount" })}
                                        mutate={async args => {
                                          console.log("calling mutate function...");
                                          await lmutate({ variables: args });
                                          // refetch();
                                        }}
                                      >
                                      </EnhancedTable>
                                    );
                                  }
                                }
                              </Query>
                            );
                          }}
                        </Mutation>
                      );
                    }}
                  </Query>
                );
              }}
            </Mutation>
          </Grid>
          <Grid item xs={12}>
            <TableWrapper
              tableMutation={PROPERTY_TABLE}
              tableQuery={PROPERTY_TABLE_QUERY}
              dataQuery={PROPERTIES}
              defaultState={defaultState.propertyTable}
              getQueryVariables={e => ({})}
            >
              {(tableSettings, lmutate, { loading, data }) => {
                return (
                  <EnhancedTable
                    {...this.props}
                    loading={loading}
                    data={data.properties}
                    title="Properties"
                    settings={tableSettings}
                    count={(data.properties.length && data.properties[0].count) || data.properties.length}
                    columnData={propertyColumnData}
                    exclude={propertyExclude}
                    historyable={PROPERTY_CREDIT_HISTORY}
                    credit={(
                      <PropertyCreditDialog
                        {...this.props}
                        positiveHandler={(m, e, selected) => m({ variables: { ...e, id: selected } })}
                      >
                      </PropertyCreditDialog>
                    )}
                    // menu={<EnhancedMenu/>}
                    options={{
                      onEdit: e => history.push(`/property/edit/${e}`),
                      onCredit: async (fn, args) => {
                        console.log("calling credit mutate function...");
                        await fn({ variables: args });
                        // refetch();
                      }
                    }}
                    render={commanRender({
                      match: "totalReceivedAmount", renderValue: (v, k) => {
                        if (k === 'nextDueDate') {
                          return formatDate(v);
                        }
                        return v === null ? "-" : v;
                      }
                    })}
                    mutate={async args => {
                      console.log("calling mutate function...");
                      await lmutate({ variables: args });
                      // refetch();
                    }}>
                  </EnhancedTable>
                );
              }}
            </TableWrapper>
          </Grid>
        </Grid>
        <Grid container justify="space-between" className={classes.fab}>
          <Grid item xs={4}>
            <Button variant="fab" color="primary" component={Link} to="/site"><AddIcon /></Button>
          </Grid>
          <Grid item xs={4}>
            <Button variant="fab" color="primary" component={Link} to="/createUser"><UserAddIcon /></Button>
          </Grid>
          <Grid item xs={4}>
            <Button variant="fab" color="primary" component={Link} to="/createProperty"><AccountBalance /></Button>
          </Grid>
        </Grid>
      </div >
    );
  }
}

export default withStyles(styles)(Dashboard);



