import React, { PureComponent } from 'react';
import Mutation from '../components/Mutation';
import Query from '../components/Query';
import Grid from '@material-ui/core/Grid';
import { withStyles } from '@material-ui/core/styles';

import EnhancedTable from '../components/EnhancedTable';
// import { defaultState } from '../graphql/resolvers';
import { SITES } from '../graphql/query/remote';
import { SITE_TABLE as SITE_TABLE_QUERY } from '../graphql/query/local';
import { SITE_TABLE } from '../graphql/mutation/local';
import { siteColumnData, siteExclude } from './Dashboard';
import User from '../components/User';
import commanRender from '../utils/commanRender';

const styles = theme => ({
    root: {
        width: '100%',
        // padding: '0',
    },
    paper: {
        ...theme.mixins.gutters(),
        paddingTop: theme.spacing.unit * 2,
        paddingBottom: theme.spacing.unit * 2,
    },
    fab: {
        position: 'fixed',
        width: "8rem",
        bottom: theme.spacing.unit * 2,
        right: theme.spacing.unit * 2,
    }
});

class Dashboard extends PureComponent {
    render() {
        const { history } = this.props;
        // updateQuery(data);        
        return (
            <div>
                <User {...this.props}></User>
                <Grid container spacing={8}>
                    <Grid item xs={12}>
                        <Mutation mutation={SITE_TABLE}>
                            {(lmutate, args) => {
                                return (
                                    // <Comp {...data} {...props} ></Comp>
                                    <Query query={SITE_TABLE_QUERY}>
                                        {({ loading, error, data: { siteTable } }) => {
                                            return (
                                                <Query query={SITES}>
                                                    {
                                                        ({ loading, error, data, refetch }) => {
                                                            refetch();
                                                            return (
                                                                <EnhancedTable
                                                                    {...this.props}
                                                                    loading={loading}
                                                                    data={data.sites}
                                                                    title="Sites"
                                                                    settings={siteTable}
                                                                    refetch={refetch}
                                                                    count={(data.sites.length && data.sites[0].count) || data.sites.length}
                                                                    columnData={siteColumnData}
                                                                    exclude={siteExclude}
                                                                    options={{
                                                                        onView: e => history.push(`/siteDetail/${e}`)
                                                                    }}
                                                                    render={commanRender("managerSpentAmount")}
                                                                    mutate={async args => {
                                                                        console.log("calling mutate function...");
                                                                        await lmutate({ variables: args });
                                                                        refetch();
                                                                    }}
                                                                >
                                                                </EnhancedTable>
                                                            );
                                                        }
                                                    }
                                                </Query>
                                            );
                                        }}
                                    </Query>
                                );
                            }}
                        </Mutation>
                    </Grid>
                </Grid>
            </div>
        );
    }
}

export default withStyles(styles)(Dashboard);



